<template>
	<div>
		<van-notice-bar left-icon="volume-o" text="" />
		<div id="echarts" style="height:250px; top: 82px;"></div>
		<p style="position: absolute;top:286px;">3</p>
		<div id="map" style="height:300px ;top:70px"></div>
	</div>
</template>

<script>
	import * as echarts from 'echarts'

	export default {
		mounted() {

			let instance = echarts.init(document.getElementById('echarts'));

			var option = {
				title: {
					text: '30',
					link: 'http://www.baidu.com'
				},
				tooltip: {},
				legend: {
					data: ['']
				},
				xAxis: {
					data: ["", "", "", "", "", ""]
				},
				yAxis: {},
				series: [{
					name: '',
					type: 'bar',
					data: [500, 200, 360, 1000, 100, 200]
				}]
			};
			//
			instance.setOption(option);


			var map = new window.BMapGL.Map('map'); // Map
			map.centerAndZoom(new window.BMapGL.Point(121.49824642727945, 31.38456001767598), 16); // ,
			map.enableScrollWheelZoom(true); // 

			var marker1 = new window.BMapGL.Marker(new window.BMapGL.Point(121.49824642727945, 31.38456001767598));
			map.addOverlay(marker1);
		}
	}
</script>

<style scoped>
	.van-notice-bar {
		position: absolute;
		top: 0.7rem;
		width: 6.2rem;
	}
</style>
