import Vue from 'vue'

//1. 
import VueRouter from 'vue-router'

//2. Vue, 
Vue.use(VueRouter);

let router = new VueRouter({
  mode:'history',
});

import home from '../pages/home.vue'
// import follow from '../pages/follow.vue'
const follow = ()=>import(/* webpackChunkName: "follow" */ "../pages/follow.vue");
// import column from '../pages/column.vue'

import detail from '../pages/detail.vue'
import login from '../pages/login.vue'
import user from '../pages/user.vue'
import reg from '../pages/reg.vue'
import NoPage from '../pages/no-page.vue'
import search from '../pages/search.vue'
import indexbar from '../pages/indexbar.vue'

//
router.addRoutes([
  {path:'/home',component:home,meta:''},
  {path:'/follow',component:follow,meta:''},
  {path:'/column',component:(r) => { require(['../pages/column.vue'],r) } ,meta:''},
  {path:'/detail/:_id',component:detail,meta:''},
  {path:'/user',component:user,meta:''},
  {path:'/login',component:login,meta:''},
  {path:'/reg',component:reg,meta:''},
  {path:'/search',component:search,meta:''},
  {path:'/indexbar',component:indexbar,meta:''},
  {path:'/',redirect:'/home'},
  {path:'*',component:NoPage,meta:''},
])


export default router
